<template>
    <div class="login">
        <h1 class="title" :class="{hidden:this.loading}">skultes <br><span>gym</span></h1>
        <LoadingSpinner v-if="this.loading" />
        <form v-else class="input-fields" @submit.prevent="login">
            <input v-model="user" type="email" name="pass" placeholder="" autocomplete="email" required>
            <input v-model="pass" type="password" name="email" placeholder="" autocomplete="current-password" required>
            <input type="submit" class="btn-rounded-clear" value="Pieslēgties">
        </form>
    </div>
</template>
<script>
import LoadingSpinner from "@/components/LoadingSpinner.vue";
import { getAuth, signInWithEmailAndPassword, onAuthStateChanged } from "firebase/auth";
export default {
    name: 'login',
    components:{ LoadingSpinner },
    data(){
        return{
            user: '',
            pass: '',
            auth: null,
            loading: false
        }
    },
    methods:{
        login(){
            this.loading = true
            signInWithEmailAndPassword(this.auth, this.user, this.pass).then(
                user => {
                    this.$router.push('/admin')
                },
                err => {
                    this.loading = false
                    alert(err.message)
                }
            )
        }
    },
    mounted(){
        this.auth = getAuth()
        onAuthStateChanged(this.auth, (user) => {
            if(user) {
                this.$router.push('/admin')
            }
        }); 
    }
}
</script>
<style scoped lang="scss">
    @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200&display=swap');
    @import '../components/colors';
    @import url('https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
    
    .login{
        padding-top: 35vh;
    }

    .title{
        font-family: 'Jost', sans-serif;
        text-transform: uppercase;
        font-size: 80px;
        line-height: 60px;
        font-style: italic;
        font-weight: 700;
        margin: 0 auto;
        width: 400px;
        margin-bottom: 40px;

        span{
        font-size: 80px;
        line-height: 54px;
        }

        &.hidden{
            margin-bottom: 70px;
        }
    }

    .input-fields{
        display: flex;
        flex-direction: column;
        width: 300px;
        margin: 0 auto;
        align-items: center;

        input{
            width: 80%;
            margin-bottom: 10px;
            color: $color1;
            background-color: $color4; 
            border: unset;
            border-bottom: 1px solid $color1;
            padding: 10px;
            font-size: 20px;
        }

        .btn-rounded-clear{
            font-size: 18px;
            background-color: transparent;
            font-weight: 500;
            color: $color2;
            border: 1px double $color2;
            padding: 5px 5px 5px 5px;
            width: 200px;
            font-family: 'Poppins', sans-serif;
            margin-top: 10px;
            cursor: pointer;
        }
    }
</style>