    <h1 class="title">skultes <br><span>gym</span></h1>
    <LoadingSpinner v-if="this.$store.getters.loadingStatus" />
    <div v-else>
      <button @click="scrollDown" class="btn-rounded-clear">Pieteikties &#x203A;</button>
      <Availability/>
      <InputForm/>
      <Cart v-if="this.$store.getters.cart.length > 0" :payment="true"/>
      <footer><a href="tel:+37127804609">&phone; Atis Ozols</a></footer>
    </div>
    <span ref="bottom"></span>
  </div>
</template>

<script>
import Cart from '@/components/Cart.vue'
import LoadingSpinner from '@/components/LoadingSpinner.vue'
import InputForm from '@/components/InputForm.vue'
import Availability from '@/components/Availability.vue'

export default {
  name: 'HomeView',
  components: {
    Cart, InputForm, LoadingSpinner, Availability
  }, 
  data(){
    return{

    }
  },
  mounted(){
    this.$store.dispatch('fetchAvailability')
  },
  methods:{
    scrollDown(){
      const el = this.$refs.bottom

      if(el){
        el.scrollIntoView({ behavior: "smooth"})
      }
      else{
        console.log('error')
      }
    },
  },
  computed:{
    startingDate(){
      const today = new Date();
      const currentDate = new Date(today);
      currentDate.setDate(today.getDate());
      const dateValue = currentDate.toLocaleDateString('en-US', { year: 'numeric', month: '2-digit', day: '2-digit' });
      const [month, day, year] = dateValue.split('/');
      const dateVal = parseInt(year+month+day)
      return dateVal
    },
  },
}
</script>

