<template>
  <div class="success" :class="{hidden:!this.status}">
    <LoadingSpinner v-if="!this.status" />
    <div v-else>
      <div v-if="this.status === 'paid'">
        <p class="thank-you">
          Paldies!
        </p>
        <p>
          Maksājums ir veiksmīgs
        </p>
        <router-link to="/">Uz sākumu</router-link>
      </div>
      <div v-else-if="this.status === 'unknown'">
        <p class="thank-you">
          Kļūda!
        </p>
        <p>
          Maksājums nav atrasts
        </p>
        <router-link to="/">Uz sākumu</router-link>
      </div>
      <div v-else-if="this.status === 'unpaid' || this.status === 'no_payment_required'">
        <p class="thank-you">
          Lūdzu, uzgaidiet!
        </p>
        <p>
          Maksājums tiek apstrādāts
        </p>
        <router-link to="/">Uz sākumu</router-link>
      </div>     
    </div>
  </div>
</template>

<script>
import LoadingSpinner from '@/components/LoadingSpinner.vue'
const utilities = require('../utilities/utilities')

export default {
  name: 'SuccessView',
  components: {
    LoadingSpinner
  }, 
  data(){
    return{
      status: null
    }
  },
  mounted(){
    if (this.$route.query.session_id){
      fetch(utilities.origin + "/api/checkout/" + this.$route.query.session_id)
                .then(response => {
                    if(response.ok) return response.json()
                    return response.json().then(json => Promise.reject(json))
                })
                .then(data => {
                  this.status = data.payment_status
                })
                .catch(e => console.error(e.error))
    } else{
      this.status = "unknown"
    }
  },
}
</script>

<style scoped lang="scss">
  @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200&display=swap');
  @import '../components/colors';
  @import url('https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
  .success{
    font-family: 'Poppins', 'sans-serif';
    padding-top: 10vh;

    &.hidden{
      padding-top: 20vh;
    }
  }

  .thank-you{
    margin-bottom: 0;
    font-size: 40px;
  }

  a{
    color: $color2;
  }
</style>
